<script setup>
    import Input from 'bbUi/components/input/input.vue'
    import Button from 'bbUi/components/button/button.vue'
    import FilterDropdown from 'components/FilterDropdown.vue'
    import CustomTableExport from 'components/CustomTable/CustomTableExport.vue'
    import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
    import CustomTableManager from 'components/CustomTable/CustomTableManager.vue'

    import { computed, ref, reactive, defineEmits, defineProps, watch, nextTick } from 'vue'
    import { useEasyOffer } from '@/store/pinia/easy-offer/easyOffer'
    import Sources from 'constants/sources'
    import moment from 'moment'
    import { titleCase } from 'utils/string'
    import { formatDatabase } from 'utils/date'

    import { useUser } from 'composable/user'
    import { useNotification } from 'composable/notification'
    import { useRoute } from 'vue-router'
    import { ElMessage } from 'element-plus'

    const props = defineProps({
        table: {
            type: Object,
            default: () => ({})
        },
        tabStatuses: {
            type: Array,
            default: () => []
        }
    })

    const emit = defineEmits(['update:customTable'])
    
    const { me, dateByTimezone } = useUser()
    const easyOfferStore = useEasyOffer()
    const {
      exportDataOffersProgress,
      resetExportDataOffersProgress
    } = useNotification()

    const referralSourceOption = computed(() => easyOfferStore.$state.referralSource?.data || [])
    const itemConditionOption = computed(() => easyOfferStore.$state.conditions?.data || [])
    const payoutMethodOption = computed(() => easyOfferStore.$state.payoutMethods?.data || [])
    const courierOption = computed(() => easyOfferStore.$state.couriers?.data || [])
    const { buyback: buybackSources } = Sources

    const isDateShow = ref(false)
    const isLoadingDropdownFilter = ref(false)
    const isAppliedFilter = ref(false)

    const isShowBarcodeScan = computed(() => ['all', '1', '2'].includes(route.params.statusId))

    const exportInProcess = ref(props.table?.is_csv_download == '1')
    const exportInProcessDots = ref(props.table?.is_csv_download == '1')

    const route = useRoute()

    const filterList = computed(() => {
    return [{
        id: 'payment_method',
        name: 'Payout Method',
        value: easyOfferStore.filters.payment_method ? easyOfferStore.filters.payment_method.join(', ') : null,
        default: []
        },{
        id: 'date',
        name: 'Date',
        value: easyOfferStore.filters.date ? `${moment(easyOfferStore.filters.date[0]).format(me.value?.date_format || '')} - ${moment(easyOfferStore.filters.date[1]).format(me.value?.date_format || '')}` : null,
        default: null
        },{
        id: 'shipping_courier',
        name: 'Courier',
        value: easyOfferStore.filters.shipping_courier ? easyOfferStore.filters.shipping_courier.join(', ') : null,
        default: []
        }, {
        id: 'buyback_source',
        name: 'Buyback Source',
        value: easyOfferStore.filters.buyback_source ? easyOfferStore.filters.buyback_source.join(', ') : null
        },{
        id: 'referral_source',
        name: 'Referral Source',
        value: easyOfferStore.filters.referral_source,
        default: null
        },{
        id: 'item_condition',
        name: 'Item Condition',
        value: easyOfferStore.filters.item_condition ? easyOfferStore.filters.item_condition.join(', ') : null,
        default: []
        }]
    })
    const isFiltered = computed(() => {
        return filterList.value.filter(filter => filter.value).length > 0
    })
    const clearFilter = (filter) => {
        if( !easyOfferStore.filters.hasOwnProperty(filter.id) ) return false
        easyOfferStore.filters[filter.id] = filter.default
    }
    const clearAllFilter = () => {
        for( let filter of filterList.value ){
        easyOfferStore.filters[filter.id] = filter.default
        }
    }

    const handleDropdownItemClick = (filter) => {
      if(!filter) return
      if(filter.key === 'date') filterDateFocus()
    }

    const handleClearFilter = async (filter) => {
      clearFilter(filter)
      await handleApplyFilter()
      if(isNotAppliedFilters.value) isAppliedFilter.value = false
    }

    const handleApplyFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = true
      emit('update:customTable')
      isLoadingDropdownFilter.value = false
    }

    const handleResetFilter = async () => {
      isLoadingDropdownFilter.value = true
      isAppliedFilter.value = false
      clearAllFilter()
      emit('update:customTable')
      isLoadingDropdownFilter.value = false
    }

    const dropdownFilterOptions = [
        {
          name: 'Select Date',
          key: 'date',
          count_selected: 0
        },
        {
          name: 'Item Condition',
          key: 'condition',
          count_selected: (easyOfferStore.filters.item_condition || []).length || 0
        },
        {
          name: 'Referral Source',
          key: 'referral_source',
          count_selected: 0
        },
        {
          name: 'Payout Method',
          key: 'payout_method',
          count_selected: (easyOfferStore.filters.payment_method || []).length || 0
        },
        {
          name: 'Source',
          key: 'source',
          count_selected: (easyOfferStore.filters.buyback_source || []).length || 0
        },
        {
          name: 'Courier',
          key: 'courier',
          count_selected: (easyOfferStore.filters.shipping_courier.length || []) || 0
        }
    ]
    const isNotAppliedFilters = computed(() => {
      const appliedFilters = filterList.value.filter((filter) => !!filter.value)
      return appliedFilters.length === 0
    })

    const dropdownItemFilterLoading = () => {
      return (
        easyOfferStore.order.isLoading ||
        easyOfferStore.orderStatus.isLoading
      )
    }

    const dropdownItemFilterDisableHover = (filter) => {
      return filter.key === 'date'
    }

    const filterDateRef = ref(null)
    const filterDateOptions = reactive({
        disabledDate(time) {
        return time.getTime() > dateByTimezone.value.getTime()
        }
    })

    const formattedDateFilter = computed(() => {
      const formattedDate = {
        from: null,
        to: null
      }
      const [fromDate, toDate] = easyOfferStore.filters.date || []
      const hasDateFilter = !!fromDate && !!toDate
      if(!hasDateFilter) return formattedDate
      formattedDate.from = formatDatabase(fromDate) || null
      formattedDate.to = formatDatabase(toDate) || null
      return formattedDate
    })

    const filterDateFocus = () => {
        if (isDateShow.value) {
        isDateShow.value = false
        return
        }

        isDateShow.value = true
        filterDateRef.value.focus()
    }

    const handleUpdateCustomTable = async () => {
        emit('update:customTable')
    }

    const currentBuybackStatusName = computed(() => {
      const isOfferStatusNotActive =  route.params.category?.toLowerCase?.() !== 'active'
      const isOfferStatusExpired = route.params.subStatusId === 'expired'
      if(isOfferStatusNotActive) return 'Archive'
      if(isOfferStatusExpired) return 'Expired'
      if(route.params.statusId === 'all') return 'All'
      const hasSubStatus = route.params.statusId && route.params.subStatusId
      if(hasSubStatus){
        const parentStatus = props.tabStatuses?.find(status => status.id == route.params.statusId)
        if(!parentStatus) return ''
        const subStatuses = parentStatus.sub
        if(!subStatuses) return ''
        return subStatuses.find(subStatus => subStatus.id == route.params.subStatusId)?.short_name || ''
      }
      return props.tabStatuses?.find(status => status.id == route.params.statusId)?.short_name || ''
    })

    const exportFilename = computed(() => {
      const statusName = currentBuybackStatusName.value
      const [fromDate, toDate] = easyOfferStore.filters.date || []
      const hasDateFilter = fromDate && toDate
      if(!hasDateFilter) return statusName
      const formattedFromDate = fromDate ? formatDatabase(fromDate) : ''
      const formattedToDate = toDate ? formatDatabase(toDate) : ''
      return `${statusName} ${formattedFromDate} to ${formattedToDate}`
    })

    const loadingExport = computed(() => {
      return easyOfferStore.loading.exportCsv || easyOfferStore.loading.exportPaypallMass || easyOfferStore.loading.exportCsvTremendous || easyOfferStore.loading.exportCsvDots
    })

    const exportCSV = async (e) => {
	    easyOfferStore.export.extension = e?.extension || e?.ext || e
      if (e.isTremendous)
        await easyOfferStore.$actionExportCSVTremendous()
      else {
        await easyOfferStore.$actionExportCsv({
          status_id: route.params.subStatusId ? route.params.subStatusId : route.params.statusId || route.params.category,
          from: formattedDateFilter.value.from,
          to: formattedDateFilter.value.to,
          is_grouped: e?.grp
        })
        ElMessage({
          type: 'success',
          message: 'Your export data request is processing, it will be delivered to your email when the process is completed.'
        })
        exportInProcess.value = true
        easyOfferStore.export.extension = 'csv'
      }
    }

    const exportCSVDOTS = async (e) => {
      const payload = {
        payment_method: e.payouts.map(i => i.name),
        delivery_method: e.dots,
        extension: e.extension
      }
      await easyOfferStore.$actionExportCSVDOTS(payload, `${e.extension.toUpperCase()} DOTS`)
      ElMessage({
        type: 'success',
        message: 'Your export data has been sent'
      })
      exportInProcessDots.value = true
    }

    const exportPaypalMass = async (e) => {
      await easyOfferStore.$actionExportPaypalMass(route.params.statusId, route.params.category === 'expired' ? '1' : '0', e)
    }

    watch(() => exportDataOffersProgress.value, async (val) => {
      const routeParamsId = (route?.params?.subStatusId && route.params.subStatusId.trim() !== '')
        ? route.params.subStatusId
        : route?.params?.statusId
      if (val?.type === 'easy-offer' && val?.status_export === 1 && val?.status_id === routeParamsId) {
        exportInProcess.value = val?.is_csv_download == 0 ? false : true
        await nextTick()
        resetExportDataOffersProgress()
      }
    }, { deep: true, immediate: true })

    watch(() => JSON.stringify(props.table), (val) => {
      exportInProcess.value = val == 1
    })
</script>

<template>
    <div class="flex flex-col xl:flex-row" :class="{'mt-2': route.params.category == 'archive'}">
        <div class="flex flex-wrap flex-grow xl:mr-3 xl:mb-0 mb-3 my-2">
            <div v-if="isShowBarcodeScan" class="flex-auto mr-2 my-2">
                <Input v-model="easyOfferStore.filters.barcode" placeholder="Scan barcode/tracking #" block icon />
            </div>
            <div class="flex-auto mr-2 my-2">
                <Input v-model="easyOfferStore.filters.search" placeholder="Search Everything" block icon>
                    <template #icon>
                        <span class="material-icons">search</span>
                    </template>
                </Input>
            </div>
            <div class="xl:flex-auto mr-2 my-2">
                <FilterDropdown
                    @item-click="handleDropdownItemClick"
                    @apply-filter="handleApplyFilter"
                    @reset-filter="handleResetFilter"
                    @collapse="isDateShow = false"
                    :filters="dropdownFilterOptions"
                    :item-loading="dropdownItemFilterLoading"
                    :item-disable-hover="dropdownItemFilterDisableHover"
                    :loading="isLoadingDropdownFilter"
                    :reset-disabled="!isAppliedFilter"
                >
                <template #label="{ item }">
                    <div>
                    <p class="text-grey-03">
                        {{ item.name }}
                        <span v-if="item.meta.count_selected > 0">
                        ({{ item.meta.count_selected }})
                    </span>
                    </p>
                    <DatePicker
                        v-if="item.key === 'date'"
                        v-model="easyOfferStore.filters.date"
                        v-bind="filterDateOptions"
                        placement="right"
                        type="daterange"
                        ref="filterDateRef"
                        class="hide-picker"
                    />
                    </div>
                </template>
                <template #dropdown="{ filter, collapse }">
                    <div class="flex flex-col p-3">
                    <template v-if="filter.key === 'condition'">
                        <Checkbox
                        v-for="condition in itemConditionOption"
                        v-model="easyOfferStore.filters.item_condition"
                        :key="condition"
                        :value="condition"
                        color="teal"
                        class="my-2 font-normal"
                        >
                        {{ condition }}
                        </Checkbox>
                    </template>
                    <template v-if="filter.key === 'referral_source'">
                        <a
                        v-for="(
                        source, sourceIndex
                        ) in referralSourceOption"
                        @click.prevent="
                        () => {
                            easyOfferStore.filters.referral_source = source
                            collapse()
                        }
                        "
                        :key="sourceIndex"
                        href=""
                        :class="[
                        'text-grey-03',
                        'p-3',
                        'block',
                        'hover:bg-grey-lighten',
                        easyOfferStore.filters.referral_source === source && 'font-semibold'
                        ]"
                        >
                        <p>{{ source }}</p>
                        </a>
                    </template>
                    <!-- <template v-if="filter.key === 'selling_type'">
                        <a
                        v-for="(sellingType, sellingTypeIndex) in stateSellingTypes"
                        @click.prevent="
                        () => {
                            handleSetSellingType(sellingType)
                            collapse()
                        }
                        "
                        :key="sellingTypeIndex"
                        href=""
                        :class="[
                        'text-grey-03',
                        'p-3',
                        'block hover:bg-grey-lighten',
                        easyOfferStore.selling_type.value === sellingType.selling_type &&
                            'font-semibold'
                        ]"
                        >
                        <p>{{ sellingType.name }}</p>
                        </a>
                    </template> -->
                    <template v-if="filter.key === 'payout_method'">
                        <Checkbox
                        v-for="payoutMethod in payoutMethodOption"
                        v-model="easyOfferStore.filters.payment_method"
                        :key="payoutMethod.id"
                        :value="payoutMethod.name"
                        color="teal"
                        class="my-2 font-normal"
                        >
                        {{ payoutMethod.name }}
                        </Checkbox>
                    </template>
                    <template v-if="filter.key === 'source'">
                        <Checkbox
                        v-for="source in buybackSources"
                        v-model="easyOfferStore.filters.buyback_source"
                        :key="source.value"
                        :value="source.value"
                        color="teal"
                        class="my-2 font-normal"
                        >
                        {{ source.name }}
                        </Checkbox>
                    </template>
                    <template v-if="filter.key === 'courier'">
                        <Checkbox
                        v-for="courier in courierOption"
                        v-model="easyOfferStore.filters.shipping_courier"
                        :key="courier"
                        :value="courier"
                        color="teal"
                        class="my-2 font-normal"
                        >
                        {{ courier }}
                        </Checkbox>
                    </template>
                    <!-- <template v-if="filter.key === 'sales_person'">
                        <Checkbox
                        v-for="salesPerson in filterOptions.sales_person"
                        v-model="easyOfferStore.sales_person_name"
                        :key="salesPerson"
                        :value="salesPerson"
                        color="teal"
                        class="my-2 font-normal"
                        >
                        {{ salesPerson }}
                        </Checkbox>
                    </template> -->
                    <!-- <template v-if="filter.key === 'partner'">
                        <a
                        v-for="partner in partners"
                        @click.prevent="
                        () => {
                            handleSetPartner(partner)
                            collapse()
                        }
                        "
                        :key="partner.id"
                        href=""
                        :class="[
                            'p-3',
                            'text-grey-03',
                            'block',
                            'hover:bg-grey-lighten',
                            easyOfferStore.partner.value === partner.id ? 'font-semibold' : ''
                        ]"
                        >
                        <p>{{ partner.name }}</p>
                        </a>
                    </template> -->
                    </div>
                </template>
                </FilterDropdown>
            </div>
            </div>
        <div class="inline-flex flex-wrap xl:flex-grow-0 flex-grow items-center my-2">
            <CustomTableManager
                @update-table="handleUpdateCustomTable"
                type="easy-offer"
                :status="route.params.subStatusId || route.params.statusId || route.params.category"
                class="mr-2"
            />
            <CustomTableExport
            v-model="easyOfferStore.export.extension"
            @export="exportCSV"
            @export-payout="exportPaypalMass"
            @export-dots="exportCSVDOTS"
            :loading="loadingExport"
            :export-filename="exportFilename"
            :with-payout="route.params.statusId == 7"
            :payouts="payoutMethodOption"
            class="w-max"
            grouped
        >
            <template #default="{ openModal, openModalPayout }">
            <div v-if="route.params.statusId == 7">
                <Dropdown
                trigger="click"
                size="medium"
                placement="bottom-end"
                :hide-on-click="true"
                :disabled="loadingExport || easyOfferStore.loading.exportCsv"
                >
                <Button
                    :loading="loadingExport || easyOfferStore.loading.exportCsv"
                    color="white">
                    <div class="flex items-center justify-between gap-x-2">
                    <span class="text-grey-03 text-base">
                        <i class="material-icons">download</i> Export
                    </span>
                    <i class="material-icons text-grey-03">arrow_drop_down</i>
                    </div>
                </Button>
                <template #dropdown>
                    <DropdownMenu>
                    <DropdownItem>
                        <Popover
                        width="400"
                        trigger="hover"
                        :disabled="!exportInProcess"
                        >
                        <div class="w-full h-full flex justify-center items-center">
                            <span class="whitespace-pre-line">
                            Export data is being processed
                            </span>
                        </div>
                        <template #reference>
                            <span v-if="!exportInProcess" @click="openModal()">Current Table View</span>
                            <span v-else class="opacity-50">Current Table View</span>
                        </template>
                        </Popover>
                    </DropdownItem>
                    <DropdownItem>
                        <span @click="openModalPayout">Only Payout Info</span>
                    </DropdownItem>
                    <DropdownItem @click="openModal(true)">Tremendous</DropdownItem>
                    </DropdownMenu>
                </template>
                </Dropdown>
            </div>
            <div v-else>
                <Popover
                width="400"
                trigger="hover"
                :disabled="!exportInProcess"
                >
                <div class="w-full h-full flex justify-center items-center">
                    <span class="whitespace-pre-line">
                    Export data is being processed
                    </span>
                </div>
                <template #reference>
                    <div>
                    <Button
                        @click="openModal()"
                        color="white"
                        block
                        :loading="loadingExport || easyOfferStore.loading.exportCsv"
                        :disabled="exportInProcess">
                        <span class="text-grey-03">
                        <i class="material-icons text-lg">
                            file_download
                        </i>
                        Export
                        </span>
                    </Button>
                    </div>
                </template>
                </Popover>
            </div>
            </template>
        </CustomTableExport>
        </div>
        </div>
        <div class="flex flex-start flex-row mb-4">
        <template v-if="isAppliedFilter">
          <div class="inline-block" v-for="(filter, filterKey) of filterList" :key="filterKey">
            <Button v-if="filter.value" color="grey" view="smooth" dense class="mx-1">
              <b>{{filter.name}}:</b> {{ filter.name === 'Referral Source' ? filter.value : titleCase(filter.value) }}<i class="material-icons text-sm ml-2" @click="handleClearFilter(filter)">close</i>
            </Button>
          </div>
          <div class="inline-block mx-2" v-if="isFiltered">
            <Button @click="handleResetFilter" color="teal" view="smooth" dense>
              <i class="material-icons text-sm mr-1">close</i>Clear all
            </Button>
          </div>
        </template>
        <!-- <div class="ml-auto flex flex-end flex-col" v-if="false">
          v-if="route.params.statusId == 2"
          <Tooltip placement="top" :disabled="table.is_intransit_processing == false && !isClickedIntransitUpdate">
            <template #content>
              <div style="max-width: 250px;">In-transit update are being processed. Please come back later or refresh the page.</div>
            </template>
            <div>
              <Button class="block ml-auto w-full" :loading="loading.all || isClickedIntransitUpdate || table.is_intransit_processing == true" color="teal" dense @click="inTransitUpdate">
                <i class="material-icons text-base mr-2">sync</i>Update In-Transit
              </Button>
            </div>
          </Tooltip>
          <small class="block mt-2 ml-auto" v-if="table?.in_transit_last_update && table.is_intransit_processing == false && isClickedIntransitUpdate == 0">Last Update: {{table.in_transit_last_update}}</small>
        </div> -->
      </div>
</template>