<script setup>
    import Card from 'bbUi/components/card/card.vue'
    import Filters from 'components/Features/EasyOffer/EasyOfferTableFilters.vue'
    import BTable from 'components/BTable/BTable.vue'
    import Pagination from 'components/Pagination.vue'
    import EasyOfferTab from 'components/Features/EasyOffer/EasyOfferTab.vue'
    import Button from 'bbUi/components/button/button.vue'
    import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
    import BulkStatusModal from 'components/Features/EasyOffer/Modals/BulkStatusModal.vue'
    import ArchiveModal from 'components/Features/EasyOffer/Modals/BulkArchiveModal.vue'
    import UnarchiveModal from 'components/Features/EasyOffer/Modals/BulkUnarchiveModal.vue'
    import Badge from 'bbUi/components/badge/badge.vue'
    import BuybackNotesWarning from '@/components/Features/Buyback/BuybackNotes/BuybackNotesWarning.vue'

    import { onMounted, watch, computed, ref, reactive } from 'vue'
    import { useRoute, useRouter } from 'vue-router'
    import debounce from 'utils/debounce'
    import { ElMessage } from 'element-plus'
    import { useEasyOffer } from '@/store/pinia/easy-offer/easyOffer'
    import { color } from 'utils/status'
    import ExpeditedIcon from 'components/ExpeditedPayout/ExpeditedIcon.vue'
    import { onUnmounted } from 'vue'
    import { useBuybackNotes } from  '@/store/pinia/buyback/notes'

    const storePiniaNotes = useBuybackNotes()
    const bulkStatusData = reactive({
      selected_ids: [],
      selected_box_id: null,
      transaction_id: null,
      notes: null,
      status_id: 1,
      is_send_email: false
    })

    const route = useRoute()
    const router = useRouter()
    const easyOfferStore = useEasyOffer()

    const tabStatuses = ref([])
    const selectedBuybacks = ref([])

    const currentTabId = computed(() => route.params.category)
    const bulkStatusModal = ref(false)
    const bulkArchiveModal = ref(false)
    const bulkUnarchiveModal = ref(false)

    const refBulkStatusForm = ref(false)
    const isLoading = ref(false)

    const handleArchiveModal = () => {
      bulkArchiveModal.value = true;
    }

    const handleUnarchiveModal = () => {
      bulkUnarchiveModal.value = true;
    }

    const bulkPrintAddressLabel = async () => {
      await easyOfferStore.$actionBatchPrintAddressLabel(selectedBuybacks.value)
    }

    const getCurrentStatusId = () => {
      if (route.params.subStatusId === 'expired') {
        return 1
      }
      return route.params.subStatusId || route.params.statusId || route.params.category
    }

    const loadData = async () => {
        if (isLoading.value) return;
        isLoading.value = true;
        
        try {
            const statusId = getCurrentStatusId();
            
            if (route.params.subStatusId === 'expired') {
                easyOfferStore.filters.is_expired = 1;
            }
            
            const isExpired = easyOfferStore.filters.is_expired;
            const isLeads = route.params.statusId == '1001'
            const isHasSubStatusParam = !!route.params.subStatusId

            if (isLeads && !isHasSubStatusParam) route.params.subStatusId = '1'
            
            const results = await Promise.all([
                easyOfferStore.$getOrderStatus(statusId),
                easyOfferStore.$getStatusOptions(),
                easyOfferStore.$getOrder(statusId),
                easyOfferStore.$getReferralSource({
                    status_id: statusId,
                    is_expired: isExpired
                }),
                easyOfferStore.$getItemCondition({
                    status_id: statusId,
                    is_expired: isExpired
                }),
                easyOfferStore.$getCustomTable(statusId),
                easyOfferStore.$getPayoutMethod(),
                easyOfferStore.$getShippingCourier({
                    status_id: statusId,
                    is_expired: isExpired
                }),
                easyOfferStore.$actionGetFileRequired()
            ]);
            
            tabStatuses.value = results[0];
        } finally {
            isLoading.value = false;
        }
    }

    const bulkStatusUnsetData = () => {
        Object.assign(bulkStatusData, {
            selected_box_id: null,
            transaction_id: null,
            notes: null,
            status_id: 1,
            is_send_email: false
        })
    }

    const updateStatusBulk = async () => {
      // 3 => FOR COMPLETED
      // 7 => FOR PAYOUT READY/PAYOUT PROCESSING
      if ([3, 7].includes(bulkStatusData.status_id)) {
        easyOfferStore.$state.loading.bulkStatus = true
        storePiniaNotes.warningNotes.ids = selectedBuybacks.value.map(i => i.order_id)
        !storePiniaNotes.warningNotes.isReviewed && await storePiniaNotes.$getNotesUnread(bulkStatusData.transaction_id.map(i => i.box_id))
        if (storePiniaNotes.warningNotes?.notes?.length) {
          storePiniaNotes.warningNotes.modal = true
          easyOfferStore.$state.loading.bulkStatus = false
          return
        }
      }

      await easyOfferStore.$actionUpdate(bulkStatusData).then(async () => {
        selectedBuybacks.value = [];
        bulkStatusModal.value = false;
        ElMessage({
          type: 'success',
          message: 'Success update status'
        })
        await loadData();
      });
    }

    const isSameSelectedStatus = computed(() => {
      const selectedData = selectedBuybacks.value
      if (!selectedData.length) return false;
      
      return selectedData.every((val) => {
        /**
         * Select Status On Hold
         * 9 = General hold
         * 15 = Activation Lock Hold
         */
        if ([9,15].includes(selectedData[0].status_id)) {
          return [9,15].includes(val.status_id)
        }

        /**
         * Select Status Not As Described
         * 12 = Revised Offer
         * 13 = Seller Accepted
         */
        else if ([12,13].includes(selectedData[0].status_id)) {
          return [12,13].includes(val.status_id)
        }

        else {
          return val.status_id === selectedData[0].status_id
        }
      })
    })

    const isShowChangeStatus = computed(() => {
      const selectedReadonly = [...selectedBuybacks.value].filter(item => item.is_readonly)
      return selectedReadonly.length === 0
    })

    const isBulkStatusLargeModal = computed(() => [7].includes(bulkStatusData.status_id))

    const handleChangeBulkStatus = () => {
        bulkStatusData.selected_box_id = selectedBuybacks.value.map(buyback => buyback.offer_id).join(',')
        bulkStatusData.transaction_id = selectedBuybacks.value.map(buyback => ({box_id: buyback.offer_id, transaction_id: ''}))
        bulkStatusModal.value = true
        
        const defaultStatusId = selectedBuybacks.value?.length > 0 ? selectedBuybacks.value[0]?.status_id : 1
        
        if (route.params.statusId === 'all') {
            bulkStatusData.status_id = defaultStatusId
        } else {
            bulkStatusData.status_id = route.params.subStatusId 
                ? parseInt(route.params.subStatusId) 
                : parseInt(route.params.statusId)
        }
    }

    const table = computed(() => {
        return easyOfferStore?.order.data
    })

    const handleBulkArchive = async () => {
        const orderIds = selectedBuybacks.value.map(buyback => buyback.order_id).join(',')
        const response = await easyOfferStore.$actionDelete([orderIds])
        
        if(response === 'deleted') {
            ElMessage({
                type: 'success',
                message: 'Success archiving offer'
            })
            selectedBuybacks.value = []
            await loadData()
        } 
    }

    const handleBulkRestore = async () => {
        const orderIds = selectedBuybacks.value.map(buyback => buyback.order_id).join(',')
        await easyOfferStore.$actionRestore([orderIds])
        
        ElMessage({
            type: 'success',
            message: 'Success restoring offer'
        })
        
        selectedBuybacks.value = []
        await loadData()
    }

    const redirectToOrder = (row) => {
        router.push({
            name: 'easyOfferDetail',
            params: {
                buybackId: row.offer_id
            }
        })
    }

    const handleUpdateFileRequired = async (value) => {
      try {
        await easyOfferStore.$actionUpdateFileRequired({
          is_required: value
        })  
      } catch (error) {
        console.log(error)
      } finally {
        ElMessage({
          type: 'success',
          message: 'Success update required file upload'
        })
        await easyOfferStore.$actionGetFileRequired()
      }
    }

    // Unified route watcher
    let routeWatchTimeout = null;
    
    watch(
        () => ({ 
            category: route.params.category, 
            statusId: route.params.statusId,
            subStatusId: route.params.subStatusId 
        }),
        (newVal, oldVal) => {
            if (routeWatchTimeout) {
                clearTimeout(routeWatchTimeout);
            }
            
            if (oldVal.subStatusId === 'expired' && newVal.subStatusId !== 'expired') {
                easyOfferStore.filters.is_expired = 0;
            }
            
            routeWatchTimeout = setTimeout(() => {
                loadData();
            }, 10);
        },
        { deep: true }
    );
    
    let filterWatchTimeout = null;
    
    watch(
        () => ({ 
            page: easyOfferStore.filters.page,
            limit: easyOfferStore.filters.limit,
            search: easyOfferStore.filters.search,
            barcode: easyOfferStore.filters.barcode,
            is_require_file: easyOfferStore.filters.is_require_file,
            is_expired: easyOfferStore.filters.is_expired
        }),
        debounce((newVal, oldVal) => {
            if (filterWatchTimeout) {
                clearTimeout(filterWatchTimeout);
            }
            
            const resetFields = ['search', 'barcode', 'limit', 'status_id', 'is_expired', 'buyback'];
            const shouldReset = resetFields.some(key => newVal[key] !== oldVal[key]);
            
            if (shouldReset) {
                easyOfferStore.filters.page = 1;
            }
            
            if (route.params.subStatusId === 'expired' && easyOfferStore.filters.is_expired !== 1) {
                easyOfferStore.filters.is_expired = 1;
            }
            
            filterWatchTimeout = setTimeout(() => {
                if (JSON.stringify(newVal) !== JSON.stringify(oldVal)) {
                    loadData();
                }
            }, 10);
        }, 500),
        { deep: true }
    );
    
    onMounted(() => {
        loadData();
    });
    
    onUnmounted(() => {
        if (routeWatchTimeout) clearTimeout(routeWatchTimeout);
        if (filterWatchTimeout) clearTimeout(filterWatchTimeout);
    });
</script>

<template>
    <div class="flex items-center gap-4 mb-6">
      <h5>Easy Offer</h5>
      <div class="px-3 py-1 bg-teal-04 text-white rounded-full flex items-center justify-center box-border text-xs">
					API
			</div>
    </div>
    <Card>
        <EasyOfferTab 
          :currentTabId="currentTabId"
          :statusId="route.params.statusId || 'all'"
          :tabStatuses="tabStatuses"
          />

        <Filters
            @update:customTable="loadData"
            :table="table"
            :tabStatuses="tabStatuses"
        />
        <div class="flex items-center">
            <Checkbox
                v-model="easyOfferStore.fileRequired.data"
                value="1"
                value-off="0"
                :loading="easyOfferStore.fileRequired.isLoading"
                type="toggle"
                @update:modelValue="handleUpdateFileRequired"
            >
                Require File Upload
            </Checkbox>
            <Tooltip placement="top">
                <template #content>
                    <div class="text-sm w-64">When its turned on, your customers must include a file when creating an EasyOffer on your front end.</div>
                </template>
                <template #default>
                    <i class="material-icons text-teal-04">info</i>
                </template>
            </Tooltip>
        </div>
        <div class="flex items-center flex-row my-4 float-left">
            <div
            class="inline-block mx-2"
            v-if="
                route.params.category === 'active' &&
                selectedBuybacks.length > 0 &&
                isSameSelectedStatus &&
                isShowChangeStatus
            "
            >
            <Button color="teal" dense @click="handleChangeBulkStatus"
            >Change Status</Button
            >
            </div>
            <div
            class="inline-block"
            v-if="route.params.category === 'active' && selectedBuybacks.length > 0"
            >
            <Button
                color="teal"
                dense
                @click="bulkPrintAddressLabel"
                :loading="easyOfferStore.$state.loading.print.address_label"
            ><i class="material-icons text-lg mr-2">print</i> Print Batch
                Address Label
            </Button>
            </div>
            <div class="inline-block mx-2" v-if="route.params.category == 'active' && selectedBuybacks.length > 0">
                <Button color="red" dense @click="handleArchiveModal"><i class="material-icons text-lg mr-2">archive</i> Archive All Selected</Button>
            </div>
            <div class="inline-block mx-2" v-if="route.params.category == 'archive' && selectedBuybacks.length > 0">
                <Button color="teal" dense @click="handleUnarchiveModal"><i class="material-icons text-lg mr-2">restore</i> Restore All Selected</Button>
            </div>
        </div>
        <BTable
          v-model:selected="selectedBuybacks"
          :items="table?.data || []"
          :headers="table?.header_table || []"
          :clickable-row="true"
          @row-click="redirectToOrder"
          item-value="offer_id"
          selectable
          class="my-4"
          v-loading="easyOfferStore?.order?.isLoading"
        >
        <template #column="{row, column, key}">
            <td v-if="key === `offer_id`" class="font-semibold">
              <div class="flex items-center">
                  {{ column }}
                <Popover v-if="row.shipping_label == 0 && route.params.statusId == 1" trigger="hover">
                  <span>Shipping label failed to generate</span>
                  <template #reference>
                    <div class="text-center ml-2">
                      <i class="material-icons text-orange-04 text-center">warning</i>
                    </div>
                  </template>
                </Popover>
                <Tooltip v-if="row.shipping_label_status == 2" placement="top">
                  <template #content>
                    <div style="max-width: 200px;">
                      Unable to generate shipping label due to an error.
                    </div>
                  </template>
                  <div class="text-center ml-2">
                    <i class="material-icons text-red-base text-center">warning</i>
                  </div>
                </Tooltip>
              </div>
            </td>
            <td v-else-if="key === `status`">
              <Badge :color="color(column)">
                {{ column }}
              </Badge>
            </td>
            <td v-else-if="Array.isArray(column)">
              <ul class="list">
                <template v-for="col in column">
                  <li v-if="col">
                    <span class="text-grey-03">{{ col }}</span>
                  </li>
                </template>
                <li
                  v-if="row[`${key}_has_more`]"
                  style="list-style-type: none"
                  class="my-2 text-grey-03"
                >
                  ... and more
                </li>
              </ul>
            </td>
            <td v-else-if="key === 'age_of_order'">
              {{ [column || 0, 'Day(s)'].join(' ') }}
            </td>
            <td v-else-if="key === 'expedited_payout'">
              <div v-if="column"
                class="w-max flex items-center gap-2 bg-green-lighten text-green-04 py-2 px-4 rounded-full text-sm"
                >
                <ExpeditedIcon class="text-green-04" />
                <span>Expedited Payout</span>
              </div>
              <span v-else>-</span>
            </td>
            <td v-else>{{ column || '-'}}</td>
          </template>
        </BTable>
        <Pagination v-if="table && table.total > easyOfferStore.filters.limit" v-model:page="easyOfferStore.filters.page" v-model:limit="easyOfferStore.filters.limit" :data="table" />
    </Card>

    <BulkStatusModal
        ref="refBulkStatusForm"
        :model-value="bulkStatusModal"
        @update:model-value="bulkStatusModal = $event"
        :bulk-status-data="bulkStatusData"
        @update:bulk-status-data="bulkStatusData = $event"
        :state-option-statuses="easyOfferStore.$state.statusOptions.result"
        :is-bulk-status-large-modal="isBulkStatusLargeModal"
        :bulk-status-loading="easyOfferStore.$state.loading.bulkStatus"
        @close="bulkStatusUnsetData"
        @update-status="updateStatusBulk"
    />

    <ArchiveModal
        :model-value="bulkArchiveModal"
        @update:model-value="bulkArchiveModal = $event"
        :selected-buybacks="selectedBuybacks"
        @archive="handleBulkArchive"
    />
    
    <UnarchiveModal
        :model-value="bulkUnarchiveModal"
        @update:model-value="bulkUnarchiveModal = $event"
        :selected-buybacks="selectedBuybacks"
        @restore="handleBulkRestore"
    />
    
    <BuybackNotesWarning @proceed="updateStatusBulk" />
</template>