import { defineStore } from 'pinia'
import request from 'utils/request'
import store from 'store'
import { formatDatabase } from 'utils/date'
import { printButton } from 'utils/dymo'
import { anchorDownload } from 'utils/element'

export const useEasyOffer = defineStore('easyOffer', {
    state: () => ({
        order: {
            data: null,
            isLoading: false
        },
        orderStatus: {
            isLoading: false,
            result: null,
        },
        statusOptions: {
            isLoading: false,
            result: null
        },
        export: {
            isLoading: false,
            extension: 'csv'
        },
        filters: {
            buyback: 'easy-offer',
            page: 1,
            limit: 10,
            search: '',
            sort_by: '',
            order_by: '',
            date: null,
            from: '',
            to: '',
            // status_id: 'all',
            is_require_file: 0,
            is_expired: 0,
            barcode: '',
            referral_source: null,
            item_condition: [],
            selling_type: [],
            payout_method: [],
            buyback_source: [],
            shipping_courier: [],
        },
        loading:{
            print: {
                address_label: false
            },
            bulkStatus: false,
            delete: false,
            restore: false,
            updateStatus: false,
            exportCsv: false,
            exportPaypallMass: false,
            exportCsvTremendous: false,
            exportCsvDots: false
        },
        customTable: {
            data: null,
            isLoading: false
        },
        referralSource: {
            data: null,
            isLoading: false
        },
        conditions: {
            data: null,
            isLoading: false
        },
        payoutMethods: {
            data: null,
            isLoading: false
        },
        couriers: {
            data: null,
            isLoading: false
        },
        fileRequired: {
            data: null,
            isLoading: false
        }
    }),
    actions: {
        $clearFilters() {
            this.filters = {
                buyback: 'easy-offer',
                page: 1,
                limit: 10,
                search: '',
                date: null,
                from: '',
                to: '',
                sort_by: '',
                order_by: '',
                // status_id: 'all',
                is_expired: 0,
                barcode: '',
                referral_source: null,
                item_condition: [],
                selling_type: [],
                payout_method: [],
                buyback_source: [],
                shipping_courier: [],
            }
        },
        async $getOrderStatus(statusId) {
            this.$state.orderStatus.isLoading = true
            try {
                const response = await request({
                    url: 'v1/pos/order/order-status',
                    method: 'get',
                    params: {
                        status_id: statusId,
                        ...this.filters
                    }
                })
                this.$state.orderStatus.result = response
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.orderStatus.isLoading = false
            }
        },
        async $getStatusOptions() {
            try {
                this.$state.statusOptions.isLoading = true
                const response = await request({
                    url: '/v1/pos/order/order-status',
                    method: 'get',
                    params: {
                        type: 'option',
                        buyback: 'easy-offer'
                    }
                })
                this.$state.statusOptions.result = response
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.statusOptions.isLoading = false
            }
        },
        async $getCustomTable(statusId) {
            try {
                this.$state.customTable.isLoading = true
                const response = await request({
                    url: '/v1/pos/custom-table',
                    method: 'get',
                    params: {
                        type_table: 'easy-offer',
                        status: statusId
                    }
                })
                this.$state.customTable.data = response
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.customTable.isLoading = false
            }
        },
        async $getReferralSource(payload) {
            try {
                this.$state.referralSource.isLoading = true
                const response = await request({
                    url: '/v1/pos/easy-offer/filter/referral-source',
                    method: 'get',
                    params: payload
                })
                this.$state.referralSource.data = response.map(item => item.name)
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.referralSource.isLoading = false
            }
        },
        async $getItemCondition(payload) {
            try {
                this.$state.conditions.isLoading = true
                const response = await request({
                    url: '/v1/pos/easy-offer/filter/item-condition',
                    method: 'get',
                    params: payload
                })
                this.$state.conditions.data = response.map(item => item.name)
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.conditions.isLoading = false
            }
        },
        async $getPayoutMethod() {
            try {
                this.$state.payoutMethods.isLoading = true
                const response = await request({
                    url: '/v1/pos/easy-offer/payment-method',
                    method: 'get',
                })
                this.$state.payoutMethods.data = response
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.payoutMethods.isLoading = false
            }
        },
        async $getShippingCourier(payload) {
            try {
                this.$state.couriers.isLoading = true
                const response = await request({
                    url: '/v1/pos/easy-offer/filter/courier',
                    method: 'get',
                    params: payload
                })
                this.$state.couriers.data = response.map(item => item.name)
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.couriers.isLoading = false
            }
        },
        async $getOrder(statusId) {
            const tempData = Object.assign({}, this.$state.filters)
            tempData.from = tempData.date && formatDatabase(tempData.date[0]) || null
            tempData.to = tempData.date && formatDatabase(tempData.date[1]) || null

            delete tempData.date
            try {
                this.$state.order.isLoading = true
                const response = await request({
                    url: 'v2/pos/easy-offer',
                    method: 'get',
                    params: {
                        status_id: statusId,
                        ...tempData
                    }
                })
                this.$state.order.data = response
                return response
            } catch (error) {
                console.log(error)
                return null
            } finally {
                this.$state.order.isLoading = false
            }
        },
        async $actionBatchPrintAddressLabel(buybacks){
            try{
              this.$state.loading.print.address_label = true
              const buybackIds = JSON.stringify(buybacks.map(buyback => buyback.offer_id))
              const payload = Object.assign({}, {
                ids: buybackIds
              })
              const response = await request({
                url: '/v1/pos/print/batch-address-label',
                method: 'post',
                data: payload
              })
              let textToPrint = []
              response.forEach((item, index) => {
                textToPrint[index] = {
                  fullname: '',
                  addressComponent: ''
                }
                textToPrint[index].fullname = `${item.billing_firstname || ''} ${item.billing_lastname || ''}`
                textToPrint[index].address_component = `${item.billing_address1 ? item.billing_address1.toUpperCase() : ''}\n${item.billing_city ? item.billing_city.toUpperCase() : ''} ${item.billing_state ? item.billing_state.toUpperCase() : ''} ${item.billing_zip ? item.billing_zip.toUpperCase() : ''}`
                textToPrint[index].imb = item.imb || ''
              })
              printButton(textToPrint)
            }finally{
              this.$state.loading.print.address_label = false
            }
        },
        async $actionDelete(selectedId){
            try{
              this.$state.loading.delete = true
              const response = await request({
                url: `/v1/pos/order/delete/bulk`,
                method: 'delete',
                data: {
                    selected_order_id: JSON.stringify(selectedId)
                }
              })
              return response
            }finally{
              this.$state.loading.delete = false
            }
        },
        async $actionRestore(selectedId){
            try{
              this.$state.loading.restore = true
              const response = await request({
                url: `/v1/pos/order/restore/bulk`,
                method: 'post',
                data: {
                    selected_order_id: JSON.stringify(selectedId)
                }
              })
              return response
            }finally{
              this.$state.loading.restore = false
            }
        },
        async $actionUpdate(data){
            this.$state.loading.bulkStatus = true
            try{
              const parsedBoxIds = data.transaction_id.reduce((curr, next) => {
                curr[next.box_id] = next.transaction_id
                return curr
              }, {})
              const payload = Object.assign({}, {
                ...data,
                transaction_id: JSON.stringify(parsedBoxIds)
              })
              const response = await request({
                url: '/v1/pos/order/bulk-status',
                method: 'post',
                data: {
                  ...payload,
                  _method: 'PUT'
                }
              })
              return response
            } finally {
                this.$state.loading.bulkStatus = false
            }
        },
        async $actionExportCsv(payload){
            const filterExport = {
                ...this.$state.filters
            }
            delete filterExport.limit
            delete filterExport.page

            try{
              this.$state.loading.exportCsv = true
              const response = await request({
                url: '/v1/pos/order/generate-csv',
                method: 'get',
                params: {
                    ...payload,
                    ...filterExport,
                    buyback_type: 4,
                    extension: this.$state.export.extension
                },
                responseType: 'blob'
              })
              return response
            }finally{
              this.$state.loading.exportCsv = false
            }
        },
        async $actionExportCSVTremendous(){
            try{
              this.$state.loading.exportCsvTremendous = true
              const response = await request({
                url: `/v1/pos/order/tremendous-failures/download`,
                method: 'get', 
                params: {
                    buyback_type: 4
                }
              })
              anchorDownload(response, `CSV Tremendous`, 'text/csv')
              return response
            }finally{
              this.$state.loading.exportCsvTremendous = false
            }
        },
        async $actionExportCSVDOTS(payload={}, filename='CSV DOTS',){
            try {
              this.$state.loading.exportCsvDots = true
              const response = await request({
                url: `/v1/pos/order/masspay`,
                method: 'get',
                params: {
                    ...payload,
                    buyback_type: 4,
                },
                responseType: 'blob'
              })
              anchorDownload(response, `${filename} ${payload.delivery_method || ''}`, `text/${payload.extension}`)
              return response
            } catch (error) {
              console.log(error)
              return null
            } finally {
              this.$state.loading.exportCsvDots = false
            }
        },
        async $actionExportPaypalMass(statusId, isExpired, payoutInfo){
            try{
              loading.exportPaypalMass = true
              const params = {
                status_id: statusId,
                payment_method: payoutInfo.payouts?.map(i => i.name) || data.payment_method,
                is_affiliate: 0,
                is_expired: isExpired,
                limit: data.limit,
                buyback_type: buyback === 'local' ? 2 : null,
                extension: payoutInfo.extension
              }
            const response = await request({
                url: '/v1/pos/order/paypal-mass',
                method: 'get',
                params: params,
                responseType: 'blob'
            })
              anchorDownload(response, 'Payouts Info', params.payment_method.length > 1 ? 'application/zip' : `text/${payoutInfo.extension}`)
            }finally{
              loading.exportPaypalMass = false
            }
        },
        async $actionGetFileRequired(){
            try{
              this.$state.fileRequired.isLoading = true
              const response = await request({
                url: '/v1/pos/easy-offer/required-file-upload',
                method: 'get',
              })
              this.$state.fileRequired.data = response.is_required_file_easy_offer
              return response
            }finally{
              this.$state.fileRequired.isLoading = false
            }
        },
        async $actionUpdateFileRequired(payload){
            try{
              this.$state.fileRequired.isLoading = true
              const response = await request({
                url: '/v1/pos/easy-offer/required-file-upload',
                method: 'put',
                data: payload
              })
              return response
            }finally{
              this.$state.fileRequired.isLoading = false
            }
        }
    }
})