<script setup>
import Tab from 'bbUi/components/tab/tab.vue'
import SelectableChip from 'components/SelectableChip.vue'

import { computed } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { mapTabRoute } from 'utils/mapping'
import { deepClone } from 'utils/deep-clone'

const props = defineProps({
  currentTabId: {
    type: [String, Number],
    required: true
  },
  statusId: {
    type: [String, Number],
    default: 'all'
  },
  tabStatuses: {
    type: Array,
    default: () => []
  }
})

const route = useRoute()
const router = useRouter()

// Category tabs (active/archive)
const categoryTabs = computed(() => {
  return [
    mapTabRoute({
      id: 0,
      short_name: 'Active',
    }, 'easyOfferTab', {
      category: 'active',
      statusId: props.statusId || 'all'
    }, 'id', 'short_name'),
    mapTabRoute({
      id: 2,
      short_name: 'Archive',
    }, 'easyOfferTab', {
      category: 'archive'
    }, 'id', 'short_name')
  ]
})

// Status tabs
const statusItems = computed(() => {
  return [
    mapTabRoute({
      id: 0,
      short_name: 'All'
    }, 'easyOfferTab', {
      statusId: 'all'
    }, 'id', 'short_name')
  ].concat(props.tabStatuses?.map(item => ({...mapTabRoute(item, 'easyOfferTab', {
    statusId: item.id,
  }, 'id', 'short_name'), data: item})))
})

const leadsOptions = computed(() => {
    const statuses = deepClone(props.tabStatuses)
    const leadsStatus = statuses.find(status => status.id == 1001)
    const subStatuses = !!leadsStatus ? leadsStatus.sub : null
    if(subStatuses){
        return subStatuses.map(subStatus => {
          if(subStatus.status_name === 'Abandoned Buyback') return {
            ...subStatus,
            color: 'orange',
            subStatusId: subStatus.id
          }
          else if(subStatus.status_name === 'Active Leads') return {
            ...subStatus,
            color: 'green',
            subStatusId: subStatus.id
          }
          else if(subStatus.status_name === 'Expired') return {
            ...subStatus,
            color: 'red',
            subStatusId: 'expired'
          }
          else return {
            ...subStatus,
            color: 'teal',
            subStatusId: subStatus.id
          }
        })
      }
      return []
    })

    const handleChangeSubStatus = (subStatusId) => {
        router.push({
            name: 'easyOfferTab',
            params: {
                statusId: route.params.statusId,
                category: route.params.category,
                subStatusId: subStatusId
            }
        })
        }
</script>

<template>
  <!-- Category Tab -->
  <Tab 
    view="smooth" 
    color="teal" 
    class="mt-14 md:m-0"
    :items="categoryTabs" 
    :active-id="currentTabId" 
  />
  
  <!-- Status -->
  <Tab 
    v-if="route.params.category === 'active'"
    class="mt-4" 
    view="smooth" 
    :items="statusItems"
  >
    <template #title="props">
      <div class="flex flex-row items-center justify-center">
        <span class="block">{{props.item.name}}</span>
        <div v-if="route.name !== 'customerDetail' && ![6, 3].includes(props.item.data?.id) && !['all', 'test order'].includes(props.item.name.toLowerCase())" class="p-3 ml-4 bg-teal-04 text-white rounded-full flex items-center justify-center box-border" style="min-width: 3rem;">
          {{ props.item.data?.total }}
        </div>
      </div>
    </template>
  </Tab>

  <div v-if="route.params.statusId == 1001" class="flex flex-col md:flex-row mb-6">
        <SelectableChip :modelValue="route.params.subStatusId" @change="handleChangeSubStatus" color-key="color" by="subStatusId" name="status_name" :items="leadsOptions" class="substatus-chip">
          <template #label="{item}">
            <div class="flex flex-wrap items-center">
              <span class="ml-2">{{item.status_name}}</span>
              <span v-if="item.total !== null" :class="['substatus-counter', 'flex', 'items-center', 'justify-center', 'p-3', 'bg-grey-03', 'rounded-full', 'text-white', 'ml-4', item.color && `--color-${item.color}`]" style="min-width: 3rem;">{{item.total}}</span>
            </div>
          </template>
        </SelectableChip>
      </div>
</template>

<style lang="scss" scoped>
.substatus-chip{
  .chip{
    &.--selected{
      .substatus-counter{
        &.--color-teal {
          @apply bg-teal-04;
        }
        &.--color-orange{
          @apply bg-orange-04
        }
        &.--color-red{
          @apply bg-red-04;
        }
        &.--color-green{
          @apply bg-green-04;
        }
      }
    }
  }
}
</style>