<script setup>
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

defineProps({
  modelValue: {
    type: Boolean,
    default: false
  },
  selectedBuybacks: {
    type: Array,
    default: () => []
  }
})

const emit = defineEmits(['update:modelValue', 'archive'])

const handleClose = () => {
  emit('update:modelValue', false)
}

const handleArchive = () => {
  emit('archive')
  emit('update:modelValue', false)
}
</script>

<template>
  <Modal 
    size="xs" 
    :model-value="modelValue" 
    @update:model-value="$emit('update:modelValue', $event)"
    title="Archive Offer" 
    hide-header 
    hide-footer 
    @close="handleClose"
  >
    <div class="py-10">
      <img class="mb-10 mx-auto" src="@/assets/icon/attention-orange.svg" alt="Success Icon">
      <div class="mt-8 mb-10">
        <h5 class="mb-4 text-center">Archiving {{ selectedBuybacks.length }} Offer<span v-if="selectedBuybacks.length > 1">s</span> </h5>
        <p class="text-center mb-4">
          Are you sure you want to archive {{ selectedBuybacks.length }} offer<span v-if="selectedBuybacks.length > 1">s</span> ? <br>
          Once archived offers can be removed from the "Archived" menu
        </p>
      </div>
      <div class="text-center">
        <Button view="outlined" color="teal" class="bg-white" @click="handleClose">Cancel</Button>
        <Button color="orange" class="ml-3" @click="handleArchive">Yes, Archive</Button>
      </div>
    </div>
  </Modal>
</template>