<script setup>
import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'
import BulkStatusesForm from 'components/Buyback/BulkStatusesForm.vue'
import { ref, defineEmits, defineProps } from 'vue'

const props = defineProps({
  modelValue: {
    type: Boolean,
    default: false
  },
  bulkStatusData: {
    type: Object,
    required: true
  },
  stateOptionStatuses: {
    type: Array,
    default: () => []
  },
  isBulkStatusLargeModal: {
    type: Boolean,
    default: false
  },
  bulkStatusLoading: {
    type: Boolean,
    default: false
  }
})

const emit = defineEmits(['update:modelValue', 'update:bulkStatusData', 'close', 'updateStatus'])

const refBulkStatusForm = ref(null)
const bulkStatusUnsetData = () => {
  emit('close')
}

const updateStatusBulk = () => {
  if(refBulkStatusForm.value.validate && refBulkStatusForm.value.validate() || false) {
    emit('updateStatus')
  }
}

const handleDataUpdate = (newValue) => {
  emit('update:bulkStatusData', newValue)
}
</script>

<template>
  <Modal 
    :model-value="modelValue"
    @update:model-value="$emit('update:modelValue', $event)"
    title="Change Status" 
    @close="bulkStatusUnsetData" 
    :size="isBulkStatusLargeModal ? 'lg' : 'sm'"
  >
    <template #default>
      <BulkStatusesForm 
        ref="refBulkStatusForm" 
        :statuses="stateOptionStatuses" 
        :data="bulkStatusData"
        @update:data="handleDataUpdate" 
      />
    </template>
    <template #footer>
      <Button 
        color="teal" 
        @click="updateStatusBulk" 
        :loading="bulkStatusLoading"
      >Save</Button>
    </template>
  </Modal>
</template>